/**
* GetCoveredFooter.js
* Get Covered section on the footer
*
* Displayed if user is NOT signed in
* Hide if user IS signed in
*
* @version 0.0.1
* @author [Nicolette Chan](nicolette@policypal.com)
*/
import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

import Button from 'components/FormsV3/Button/Button';

import './Footer.scss';

class GetCoveredFooter extends Component {
  render() {
    const { history, login } = this.props;
    return (
      <Col sm="12" md="8" className="sub-footer">
        <Row className="center-align">
          {/* CTA */}
          <Col sm="12" md="6" className="cta">
            <h2 className="footer-fancy-font">Insurance just got easier</h2>
            <p>Create an account to start today</p>
          </Col>
          {/* Buttons */}
          <Col sm="8" md="6" className="cta-button">
            <Row>
              <Button
                type="submit"
                variant="medium-flat"
                className="green-btn"
                width={200}
                height={72}
                onClick={() => history.push('/categories')}
              >
                <p>Get Covered</p>
              </Button>
              <Button
                type="submit"
                variant="medium-flat"
                className="white-btn"
                width={200}
                height={72}
                onClick={() => { login.popup.openPopup(login.loginContainer.setInitialIndex(2)); }}
              >
                <p>Create an account</p>
              </Button>
            </Row>
          </Col>
        </Row>
      </Col>
    );
  }
}

GetCoveredFooter.defaultProps = {
  login: null,
};

GetCoveredFooter.propTypes = {
  history: PropTypes.object.isRequired,
  login: PropTypes.object,
};

export default withRouter(GetCoveredFooter);
