/**
 * Footer.js
 * Footer for Web view
 *
 * @version 0.0.1
 * @author [Nicolette](nicolette@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';
import moment from 'moment';

// import images
import FooterPolicyPalImg from 'assets/images/Footer/footer-policy-pal.svg';
// import nav category icons
import CategoriesIcon from 'assets/images/NavIcons';
// import icons
import EmailIcon from 'assets/images/Icons/email.svg';
import PhoneIcon from 'assets/images/Icons/phone.svg';
import LocationIcon from 'assets/images/Icons/location.svg';
import FacebookIcon from 'assets/images/Icons/facebook.svg';
import TwitterIcon from 'assets/images/Icons/twitter.svg';
import InstagramIcon from 'assets/images/Icons/instagram.svg';
import LinkedInIcon from 'assets/images/Icons/linkedin.svg';

// import Like Facebook button
import FacebookLike from '../Templates/FacebookLike/FacebookLike';
// import Get Covered portion above footer
import GetCoveredFooter from './GetCoveredFooter';

import './Footer.scss';

class Footer extends Component {
  constructor(props) {
    super(props);

    this.company = [
      { name: 'Claims', url: '/claims' },
      { name: 'Partnership', url: '/partnership' },
      { name: 'Rewards', url: '/rewards' },
      { name: 'About Us', url: '/about' },
      { name: 'Career', url: '/career' },
      { name: 'FAQ', url: '/faq' },
      { name: 'Blog', url: 'https://blog.policypal.com/', type: 'external' },
    ];
    this.resources = [
      { name: 'Terms of Service', url: '/tos' },
      { name: 'Privacy Policy', url: '/privacypolicy' },
      { name: 'Fair Dealing Statement', url: '/fair-dealing-statement' },
      { name: 'Security', url: '/security' },
      { name: 'Press', url: '/press' },
    ];

    this.state = {
      year: moment().year(),
    };

    this.renderLink = this.renderLink.bind(this);
  }
  renderGetCovered() {
    const {
      userDetails,
      login,
    } = this.props;

    /* User not logged in */
    if (userDetails === null) {
      return <GetCoveredFooter login={login} />;
    }

    return null;
  }

  renderLink(item, index) {
    const { history } = this.props;
    const {
      name,
      url,
      type: type = '',
    } = item;

    if (type && type === 'external') {
      return (
        <li key={index}>
          <a
            href={url}
            className="link"
            target="__blank"
          >
            {name}
          </a>
        </li>
      );
    }

    return (
      <li key={index}>
        <button
          onClick={() => history.push(url)}
          className="link"
        >
          {name}
        </button>
      </li>
    );
  }

  render() {
    const { year } = this.state;
    const { history } = this.props;
    const {
      marginTop,
    } = this.props;
    return (
      <Col sm="12" xs="12" className="footer d-sm-none d-none d-md-none d-lg-block d-xl-block" style={{ padding: 0, marginTop }}>
        {/* Light faded PolicyPal word */}
        <Row noGutters>
          {this.renderGetCovered()}
          <Col md="12" className="footer-bg-color">
            <img src={FooterPolicyPalImg} className="footer-bg" alt="FooterPolicyPal.jpg" />
            <Row className="padding-top-60">
              <Col lg={{ size: 8, offset: 2 }} md={{ size: 10, offset: 1 }} xs="12">
                <Row>
                  {/* Contact */}
                  <Col lg="4" xs="8" className="footer-contact">
                    <Row>
                      <Col md={{ size: 12 }} lg={{ size: 12 }}>
                        {/* Email */}
                        <Row>
                          <img src={EmailIcon} className="icon" alt="EmailIcon.png" />
                          <p className="margin-left-10"><a href="mailto:hello@policypal.com">hello@policypal.com</a></p>
                        </Row>
                        {/* Phone */}
                        <Row>
                          <img src={PhoneIcon} className="icon" alt="PhoneIcon.png" />
                          <p className="margin-left-10"><a href="tel:+6591843163">+65 3163 9184</a></p>
                        </Row>
                        {/* Location */}
                        <Row>
                          <img src={LocationIcon} className="icon" alt="LocationIcon.png" />
                          <a
                            href="https://goo.gl/maps/2vf8g6LCQrt"
                            target="_blank"
                            rel="noopener noreferrer"
                          >
                            <p className="margin-left-10">
                                PolicyPal <br />
                                38 Eng Hoon Street<br />
                                Singapore 169783
                            </p>
                          </a>
                        </Row>
                        {/* Social Media */}
                        <Row style={{ paddingTop: 44 }}>
                          <Col style={{ padding: 0 }}>
                            <a href="https://www.facebook.com/hipolicypal">
                              <img src={FacebookIcon} className="social-media-icons" alt="FacebookIcon.png" />
                            </a>
                          </Col>
                          <Col style={{ padding: 0 }}>
                            <a href="https://twitter.com/hipolicypal">
                              <img src={TwitterIcon} className="social-media-icons" alt="TwitterIcon.png" />
                            </a>
                          </Col>
                          <Col style={{ padding: 0 }}>
                            <a href="https://www.instagram.com/policypal/">
                              <img src={InstagramIcon} className="social-media-icons" alt="InstagramIcon.png" />
                            </a>
                          </Col>
                          <Col style={{ padding: 0 }}>
                            <a href="https://www.linkedin.com/company/policypal/">
                              <img src={LinkedInIcon} className="social-media-icons" alt="LinkedInIcon.png" />
                            </a>
                          </Col>
                        </Row>
                        {/* Facebook Likes */}
                        <Row className="social-media-icons" style={{ marginTop: '9px' }}>
                          <FacebookLike />
                        </Row>
                      </Col>
                    </Row>
                  </Col>
                  {/* Products */}
                  <Col lg="3" className="footer-links">
                    <Row>
                      <div className="col-12 footer-link-header">PRODUCTS</div>
                      <ul>
                        {/* {
                          _.map(CategoriesIcon.color, (product, index) => (
                            <li key={index}>
                              <button
                                onClick={() => history.push(product.url)}
                                className="link"
                              >
                                {product.name}
                              </button>
                            </li>
                            ))
                        } */}
                        { _.map(CategoriesIcon.color, this.renderLink) }
                      </ul>
                    </Row>
                  </Col>
                  {/* Company */}
                  <Col lg="2" className="footer-links">
                    <div className="footer-link-header">COMPANY</div>
                    <ul className="col-12">
                      {/* {
                        _.map(this.company, (company, index) => (
                          <li key={index}>
                            <button
                              onClick={() => history.push(company.url)}
                              className="link"
                            >
                              {company.name}
                            </button>
                          </li>
                          ))
                      } */}
                      { _.map(this.company, this.renderLink) }
                    </ul>
                  </Col>
                  {/* Resources */}
                  <Col lg="2" className="footer-links">
                    <div className="footer-link-header">RESOURCES</div>
                    <ul>
                      {
                        _.map(this.resources, (resource, index) => (
                          <li key={index}>
                            <button
                              onClick={() => history.push(resource.url)}
                              className="link"
                              style={{ textAlign: 'left' }}
                            >
                              {resource.name}
                            </button>
                          </li>
                          ))
                      }
                      <li className="copyright">© PolicyPal {year}</li>
                    </ul>
                  </Col>
                </Row>
              </Col>
            </Row>
          </Col>
        </Row>
      </Col>
    );
  }
}
Footer.defaultProps = {
  userDetails: null,
  marginTop: 0,
};

Footer.propTypes = {
  history: PropTypes.object.isRequired,
  userDetails: PropTypes.object,
  marginTop: PropTypes.number,
  login: PropTypes.object.isRequired,
};

export default withRouter(connect(null, null)(Footer));
