/**
* FacebookLike.js
* Component for Facebook Like Button
*
* @version 0.0.1
* @author [Nicolette Chan](nicolette@policypal.com)
*/
import window from 'global/window';
import React, { Component } from 'react';

class FacebookLike extends Component {
  componentDidMount() {
    try {
      if (window.FB) {
        window.FB.XFBML.parse();
      }
    } catch (error) {
      // eslint-disable-next-line
      // console.log('FB like error');
    }
  }
  render() {
    return (
      <div
        className="fb-like"
        data-href="https://www.facebook.com/hipolicypal"
        data-layout="button_count"
        data-action="like"
        data-width="400"
        data-size="large"
        data-show-faces="false"
        data-share="false"
      />
    );
  }
}

export default FacebookLike;
