/**
 * FooterContainer.js
 * Swap between web and mobile footer and
 * hide creates account if logged in
 *
 * @version 0.0.1
 * @author [Nicolette](nicolette@policypal.com)
 */
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import WebFooter from '../components/Navigations/WebFooter';
import MobileFooter from '../components/Navigations/MobileFooter';

import '../components/Navigations/Footer.scss';

class FooterContainer extends Component {
  static getDerivedStateFromProps(nextProps) {
    const { userDetails } = nextProps;
    let result = null;
    if (userDetails) {
      result = { userDetails };
    }
    return result;
  }
  constructor(props) {
    super(props);
    const { userDetails } = this.props;

    this.state = {
      userDetails,
    };
  }

  render() {
    const {
      login,
    } = this.props;

    const {
      userDetails,
    } = this.state;

    if (!userDetails.data) {
      return (
        <Fragment>
          <WebFooter
            login={login}
          />
          <MobileFooter
            login={login}
          />
        </Fragment>
      );
    }
    return (
      <Fragment>
        <WebFooter
          login={login}
          userDetails={userDetails.data}
        />
        <MobileFooter
          login={login}
          userDetails={userDetails.data}
        />
      </Fragment>
    );
  }
}

FooterContainer.propTypes = {
  login: PropTypes.object.isRequired,
  userDetails: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  userDetails: state.userDetails,
  login: state.login,
});

export default connect(mapStateToProps, null)(FooterContainer);
