/**
 * Footer.js
 * Footer for Mobile view
 *
 * @version 0.0.1
 * @author [Nicolette](nicolette@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import moment from 'moment';

import {
  Collapse,
  NavLink,
  DropdownToggle,
  DropdownItem,
  UncontrolledDropdown,
  Col,
  Row,
} from 'reactstrap';

// arrow
import Arrow from 'assets/images/forms/arrow.svg';
// import images
import FooterPolicyPalImg from 'assets/images/Footer/footer-policy-pal.svg';
// import nav category icons
import CategoriesIcon from 'assets/images/NavIcons';
// import icons
import EmailIcon from 'assets/images/Icons/email.svg';
import PhoneIcon from 'assets/images/Icons/phone.svg';
import LocationIcon from 'assets/images/Icons/location.svg';
import FacebookIcon from 'assets/images/Icons/facebook.svg';
import TwitterIcon from 'assets/images/Icons/twitter.svg';
import InstagramIcon from 'assets/images/Icons/instagram.svg';
import LinkedInIcon from 'assets/images/Icons/linkedin.svg';

// import Get Covered portion above footer
import GetCoveredFooter from './GetCoveredFooter';

// import css
import './Footer.scss';
import FacebookLike from '../Templates/FacebookLike/FacebookLike';

class MobileFooter extends Component {
  constructor(props) {
    super(props);
    this.state = {
      collapseInsurance: false,
      collapseCompany: false,
      collapseResource: false,
      rotateInsuranceArrow: 'arrow',
      rotateCompanyArrow: 'arrow',
      rotateResourcesArrow: 'arrow',
      year: moment().year(),
    };
    this.toggleInsurance = this.toggleInsurance.bind(this);
    this.toggleCompany = this.toggleCompany.bind(this);
    this.toggleResource = this.toggleResource.bind(this);
    // close menu when clicked on link
    this.closeMenu = this.closeMenu.bind(this);

    // render more tab
    this.moreTab = [
      { name: 'Claims', url: '/claims' },
      { name: 'Partnership', url: '/partnership' },
      { name: 'About Us', url: '/about' },
      { name: 'Career', url: '/career' },
      { name: 'FAQ', url: '/faq' },
    ];
    this.resources = [
      { name: 'Terms of Service', url: '/tos' },
      { name: 'Privacy Policy', url: '/privacypolicy' },
      { name: 'Fair Dealing Statement', url: '/fair-dealing-statement' },
      { name: 'Security', url: '/security' },
      { name: 'Press', url: '/press' },
    ];
  }
  // closes all menu
  closeMenu() {
    this.setState({
      collapseInsurance: false,
      collapseCompany: false,
      collapseResource: false,
      rotateInsuranceArrow: 'arrow',
      rotateCompanyArrow: 'arrow',
      rotateResourcesArrow: 'arrow',
    });
  }
  toggleInsurance() {
    this.setState({
      collapseInsurance: !this.state.collapseInsurance,
      collapseCompany: false,
      collapseResource: false,
    });
  }
  toggleCompany() {
    this.setState({
      collapseInsurance: false,
      collapseCompany: !this.state.collapseCompany,
      collapseResource: false,
    });
  }
  toggleResource() {
    this.setState({
      collapseInsurance: false,
      collapseCompany: false,
      collapseResource: !this.state.collapseResource,
    });
  }
  renderGetCovered() {
    const {
      userDetails,
      login,
    } = this.props;

    /* User not logged in */
    if (userDetails === null) {
      return <GetCoveredFooter login={login} />;
    }

    return null;
  }
  renderInsuranceLink() {
    const category = CategoriesIcon.white;
    const renderLinks = _.map(category, (link, index) => (
      <NavLink
        key={`insur-${index}`}
        tag={Link}
        to={link.url}
        onClick={this.closeMenu}
      >
        <DropdownItem>
          <p>{link.name}</p>
        </DropdownItem>
      </NavLink>
    ));
    return renderLinks;
  }
  renderMoreLinks() {
    const renderMoreLinks = _.map(this.moreTab, (link, index) => (
      <NavLink
        key={`insur-more-${index}`}
        tag={Link}
        to={link.url}
        onClick={() => {
          this.closeMenu();
        }}
      >
        <DropdownItem>
          <p>{link.name}</p>
        </DropdownItem>
      </NavLink>
    ));
    return renderMoreLinks;
  }
  renderResourceLinks() {
    const renderResourcesLinks = _.map(this.resources, (link, index) => (
      <NavLink
        key={`insur-resource-${index}`}
        tag={Link}
        to={link.url}
        onClick={() => {
          this.closeMenu();
        }}
      >
        <DropdownItem>
          <p>{link.name}</p>
        </DropdownItem>
      </NavLink>
    ));
    return renderResourcesLinks;
  }
  render() {
    const { year } = this.state;
    const rotateInsuranceArrow = this.state.rotateInsuranceArrow ? 'arrow' : 'turn';
    const rotateCompanyArrow = this.state.rotateCompanyArrow ? 'arrow' : 'turn';
    const rotateResourcesArrow = this.state.rotateResourcesArrow ? 'arrow' : 'turn';

    const {
      marginTop,
    } = this.props;

    return (
      <Col sm="12" xs="12" className="footer d-md-block d-lg-none d-xl-none" style={{ padding: 0, marginTop }}>
        {/* Light faded PolicyPal word */}
        <Row noGutters>
          {this.renderGetCovered()}
          <Col md="12" className="footer-bg-color">
            <img src={FooterPolicyPalImg} className="footer-bg" alt="FooterPolicyPal.jpg" />
            <Row className="padding-top-60">
              <Col lg={{ size: 12 }} md={{ size: 12 }} xs="12">
                <Row>
                  {/* Contact */}
                  <Col lg="4" xs="12" className="footer-contact">
                    <Row>
                      <Col md={{ size: 10, offset: 1 }} lg={{ size: 10, offset: 1 }}>
                        <Row className="location-row">
                          <Col xs={{ size: 10 }} style={{ margin: 'auto' }} >
                            {/* Email */}
                            <div>
                              <img src={EmailIcon} className="icon" alt="EmailIcon.png" />
                              <p className="margin-left-10"><a href="mailto:hello@policypal.com">hello@policypal.com</a></p>
                            </div>
                            {/* Phone */}
                            <div>
                              <img src={PhoneIcon} className="icon" alt="PhoneIcon.png" />
                              <p className="margin-left-10"><a href="tel:+6591843163">+65 3163 9184</a></p>
                            </div>
                            {/* Location */}
                            <div>
                              <img src={LocationIcon} className="icon" alt="LocationIcon.png" />
                              <a
                                href="https://goo.gl/maps/2vf8g6LCQrt"
                                target="_blank"
                                rel="noopener noreferrer"
                              >
                                <p className="margin-left-10">
                                    PolicyPal <br />
                                    38 Eng Hoon Street,
                                  <br />Singapore 169783
                                </p>
                              </a>
                            </div>
                          </Col>
                        </Row>
                        {/* Social Media */}
                        <Row>
                          <Col
                            sm={{ size: 8 }}
                            xs={{ size: 10 }}
                            style={{ margin: 'auto' }}
                          >
                            <Row>
                              <Col className="social-col">
                                <a href="https://www.facebook.com/hipolicypal">
                                  <img src={FacebookIcon} className="social-media-icons" alt="FacebookIcon.png" />
                                </a>
                              </Col>
                              <Col className="social-col">
                                <a href="https://twitter.com/hipolicypal">
                                  <img src={TwitterIcon} className="social-media-icons" alt="TwitterIcon.png" />
                                </a>
                              </Col>
                              <Col className="social-col">
                                <a href="https://www.instagram.com/policypal/">
                                  <img src={InstagramIcon} className="social-media-icons" alt="InstagramIcon.png" />
                                </a>
                              </Col>
                              <Col className="social-col">
                                <a href="https://www.linkedin.com/company/policypal/">
                                  <img src={LinkedInIcon} className="social-media-icons" alt="LinkedInIcon.png" />
                                </a>
                              </Col>
                            </Row>
                          </Col>
                        </Row>
                        {/* Facebook Likes */}
                        <Row>
                          <div style={{ margin: 'auto' }}>
                            <FacebookLike />
                          </div>
                        </Row>
                      </Col>
                    </Row>
                  </Col>
                  <UncontrolledDropdown>
                    <DropdownToggle nav onClick={this.toggleInsurance}>
                      <div className="center-align">
                        <span>INSURANCES</span>
                        <img src={Arrow} alt="/" className={rotateInsuranceArrow} />
                      </div>
                    </DropdownToggle>
                    <div className="sub-menu">
                      <Collapse isOpen={this.state.collapseInsurance}>
                        {this.renderInsuranceLink()}
                      </Collapse>
                    </div>
                  </UncontrolledDropdown>
                  <UncontrolledDropdown>
                    <DropdownToggle
                      nav
                      onClick={this.toggleCompany}
                      className="mobile-footer-header"
                    >
                      <div className="center-align">
                        <span>COMPANY</span>
                        <img src={Arrow} className={rotateCompanyArrow} alt="/" />
                      </div>
                    </DropdownToggle>
                    <div className="sub-menu">
                      <Collapse isOpen={this.state.collapseCompany}>
                        {this.renderMoreLinks()}
                      </Collapse>
                    </div>
                  </UncontrolledDropdown>
                  <UncontrolledDropdown>
                    <DropdownToggle nav onClick={this.toggleResource}>
                      <div className="center-align">
                        <span>RESOURCES</span>
                        <img src={Arrow} alt="/" className={rotateResourcesArrow} />
                      </div>
                    </DropdownToggle>
                    <div className="sub-menu">
                      <Collapse isOpen={this.state.collapseResource}>
                        {this.renderResourceLinks()}
                      </Collapse>
                    </div>
                  </UncontrolledDropdown>
                  <div className="copyright">© PolicyPal {year}</div>
                </Row>
              </Col>
            </Row>
          </Col>
        </Row>
      </Col>
    );
  }
}

MobileFooter.defaultProps = {
  marginTop: 0,
  userDetails: null,
};

MobileFooter.propTypes = {
  marginTop: PropTypes.number,
  login: PropTypes.object.isRequired,
  userDetails: PropTypes.object,
};


export default MobileFooter;
